import interfaces from "../../unils/interfaces.js"
export default {
	data() {
		return {
			Goods: [], //商品数据
			filterby: "all", //默认选择全部
			page: "2",
			size: "6",
			loadingText: "拼命加载中",
			filterByList: [{
					"text": "全部",
					selected: true,
					filterby: "all"
				},
				{
					"text": "口碑",
					selected: false,
					filterby: "public"
				},
				{
					"text": "热门",
					selected: false,
					filterby: "hot"
				}
			]
		}
	},
	// 获取页面跳转参数
	onLoad(option) {
		// console.log("option",option.name)
		uni.setNavigationBarTitle({
			title: option.name
		})

		// 获取数据
		this.loadData()
	},
	// 下拉刷新
	onPullDownRefresh() {
		setTimeout(() => {
			this.page = 1;
			this.loadingText = "加载中...";
			this.Goods = [];
			this.loadData();
			uni.stopPullDownRefresh();
		}, 100)
	},
	// 上拉加载
	onReachBottom() {
		this.page++;
		this.loadData()
	},
	methods: {
		// 列表跳详情
		handleGoods(goods){
			uni.navigateTo({
				url:"./goods-detail?goodsinfo="+JSON.stringify(goods)
			})
		},
		// 导航选中下划线
		handleSelect(index) {
			console.log(index)
			this.filterByList[index].selected = true;
			for (let i = 0; i < this.filterByList.length; i++) {
				if (i != index) {
					this.filterByList[i].selected = false;
				}
			}
			
			// 切换导航,数据获取
			this.filterby=this.filterByList[index].filterby;
			this.page=1;
			this.loadingText="加载中...";
			this.Goods=[];
			this.loadData();
		},
		loadData() {
			this.request({
				url: `${interfaces.getGoods}/${this.filterby}/${this.page}/${this.size}`,
				success: ((res) => {
					// console.log(res)
					// this.Goods = res.data
					// console.log("Goods", this.Goods)
					if(res.data.length>0){
						res.data.forEach(item=>{
							this.Goods.push(item);
							// console.log("Goods",this.Goods)
						})
					}else{
						this.loadingText="到底啦~"
					}
				})
			})
		}
	}
}
